import { Routes } from '@angular/router';
import { MainpageComponent } from './mainpage/mainpage.component';
import { TrainingComponent } from './training/training.component';
import { MaterialtrainingComponent } from './materialtraining/materialtraining.component';
import { HomeComponent } from './home-component/home-component.component';
import { AboutComponent } from './about-component/about-component.component';
export const Approute: Routes = [
    {
        path: "traning",
        component: TrainingComponent
    },
    {
        path: "materialtraning",
        component: MaterialtrainingComponent
    },
    {
        path: '',
        component: MainpageComponent
    },
    {
        path: 'home',
        component: HomeComponent
    },
    {
        path: 'about',
        component: AboutComponent
    }
];